<template>
  <div class="custom-card" @touchstart="handleTouchStart"
    @touchmove="handleTouchMove"
    @touchend="handleTouchEnd" >
    <div class="card-title">
      <h2>{{ eventTitle }}</h2>
    </div>
    <div class="datetime-container">
      <p>{{ eventDate }}</p>
      <p>{{ eventTime }}</p>
    </div>
    <div class="location-container" @click="openGoogleMaps">
      <img  src="../assets/img/pinmap.png" alt="buttonpng" title="Open in maps" />
      <p>{{ place }}</p>
    </div>
    <!-- <div class="qr-code-container">
      <img :src="generateQRCode()" alt="Location QR Code" />
    </div> -->
    <div class="dot-bar">
      <span
        v-for="(dot, index) in dotArray"
        :key="index"
        class="dot"
        :class="{ 'active-dot': index === currentIndex }"
        @click="goToSlide(index)"
      ></span>
  </div>
</div>
</template>
  
  <script>
 // import QRCode from 'qrcode';
  
  export default {

  props: {
    eventTitle : String ,
    eventDate : String ,
    eventTime : String ,
    place : String ,
    location : location,
    currentIndex: Number,
    totalCards: Number
  },
  data() {
    return {
      activeDotIndex: 0,
      dotArray: Array(this.totalCards).fill(null),
      touchStartX: 0,
      touchEndX: 0
    }
  },


    methods: {
      openGoogleMaps() {
        window.open(this.location);
      },
      /* generateQRCode() {
        // Generate a QR code for the location
        const url = this.location;
        const canvas = document.createElement('canvas');
        QRCode.toCanvas(canvas, url, { width: 70 });
        return canvas.toDataURL('image/png');
      }, */
      goToSlide(index) {
        // Emit an event to notify the parent component to change the slide
        this.$emit('goToSlide', index);
      },
      handleTouchStart(event) {
        this.touchStartX = event.touches[0].clientX;
      },

      handleTouchMove(event) {
        this.touchEndX = event.touches[0].clientX;
      },

      handleTouchEnd() {
        const touchDiff = this.touchEndX - this.touchStartX;
          

        if (touchDiff < 0) {
          if(this.currentIndex+1<this.totalCards){
            this.goToSlide(this.currentIndex+1);
          }
        } else if (touchDiff > 0) {
          if(this.currentIndex-1>=0){
            this.goToSlide(this.currentIndex-1);
          }
        }
      }
    },
  };
  </script>
  
  <style scoped>
  .custom-card {
    max-width: 250px;
    max-height: 340px; /* Adjust the max-width as needed */
    width: 100%; /* Ensures the card takes the full width within its container */
    border: 1px solid #ccc;
    border-radius: 30px;
    color: rgb(107, 43, 43);
    padding: 6px;
    margin: auto; /* Centers the card and provides vertical spacing */
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    position: relative; 
    background-color: rgba(255, 255, 255, 0.5);
  }
  
  .card-title {
    font-size: 1.0em;
    font-family: 'Pacifico', cursive;
  }
  
  .datetime-container{
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  }
  .location-container{
    display: block;
    align-items: center;
    padding: 1px;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  }
.location-container img{
    max-width: 25px;
    max-height: 25px;
    padding: 1px;
  }
  .dot-bar {
        display: flex;
        justify-content: center;
    }
    .dot {
        height: 10px;
        width: 10px;
        border-radius: 50%;
        background-color: #99a8b0;
        margin: 0 5px;
        cursor: pointer;
    }
    .active-dot {
        background-color: #333;
    }
  </style>
  