<template>
  <div class="carousel" v-if="isOpen">
    <div class="carousel-inner">
      <carousel-item v-for="(slide, index) in slides"
      :slide="slide" 
      :key="'item-' + index" 
      :current-slide="currentSlide"
      :index="index" />
      <CarouselControls v-if="showControls" @prev="prev" @next="next"></CarouselControls>
    </div>
  </div>
</template>

<script>

import CarouselItem from "./CarouselItem.vue";
import CarouselControls from "./CarouselControls.vue";

export default {
  props: {
  slides: Array,
  isOpen: Boolean,
  showControls: {type: Boolean,default: true}
  },
  components: {
    CarouselItem,
    CarouselControls
  
},
  data() {
    return {
      currentSlide: 0,
      slideInterval: null
    }
  },
  methods: {
     setCurrentSlide(index) {
      this.currentSlide = index;
    },
    prev(){
      const index = this.currentSlide > 0 ? this.currentSlide-1 : this.slides.length - 1 ;
      this.setCurrentSlide(index);
    },
    next(){
      const index = this.currentSlide < this.slides.length - 1 ? this.currentSlide + 1 : 0;
      this.setCurrentSlide(index);
    } ,
    startSlideInterval() {
      this.slideInterval = setInterval(() => {
        this.next();
      }, 2000);
    },
    stopSlideInterval() {
      clearInterval(this.slideInterval);
    },
  },
  watch: {
    isOpen(newVal) {
      if (newVal && !this.showControls) {
        this.startSlideInterval();
      } else {
        this.stopSlideInterval();
      }
    },
    showControls(newVal) {
      if (this.isOpen && !newVal) {
        this.startSlideInterval();
      } else {
        this.stopSlideInterval();
      }
    }
  },
  mounted() {
    if (this.isOpen && !this.showControls) {
      this.startSlideInterval();
    }
  },
  beforeUnmount() {
    this.stopSlideInterval();
  },
};
</script>

<style>
.carousel {
  display: flex;
  justify-content: center;
  max-width: 60%;
  margin: 10%;
  position: relative;
  margin-right: auto; 
  margin-left: auto; 
}

.carousel-inner {
  position: relative;
  overflow: hidden;
  display: inline-block;
  border: 2px solid #4c5342;
  padding: 5px ; 
  border-radius: 15px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}
</style>
