<template>
    <div class="gallery">
      <transition :name="transitionName">
        <div :key="currentIndex" class="slide">
          <Card
            :eventTitle="currentCard.eventTitle"
            :eventDate="currentCard.eventDate"
            :place="currentCard.place"
            :location="currentCard.location"
            :eventTime="currentCard.eventTime"
            :currentIndex="currentIndex"
            :totalCards = "totalCards"
            @goToSlide="goToSlide"
          />
        </div>
      </transition>
    </div>
  </template>
  
  <script>
  import Card from './AdvancedCard.vue';
  
  export default {
    props: {
      cards: Array,
    },
    components: {
      Card,
    },
    data() {
      return {
        currentIndex: 0,
        transitionName: 'fade',
        totalCards:this.cards.length
      };
    },
    computed: {
      currentCard() {
        return this.cards[this.currentIndex];
      },
    },
    methods: {
      goToSlide(index) {
        this.transitionName = 'fade';
        this.currentIndex = index;
      },
    },
  };
  </script>
  
  <style scoped>
  
  </style>
  