<template>
  <router-view></router-view>
  <div id="main_pic">
    <ConfettiExplosion :particleCount="300" :stageWidth="300" :stageHeight=1800  />
    <img src="./assets/img/bg.jpg" :style="{ opacity: imageOpacity }" />
  </div>
  <div id="content">
    <div class="name_card">
      <h2>ASWATHY & GOKUL</h2>
      </div>
    <div class="intro">
      <h3>We are starting our journey</h3>
      <h3>request your presence on our precious moment</h3>
    </div>
    
    <CardGallery :cards="cardSlides" />
    <div class="picture_cards">
      <Carousal :slides="slides" :is-open="true" :showControls="showCarouselControls" />
    </div>
    <div class="regards_msg">
      <p>Regards from Vishnu, Gopika & Arjun</p>
      
    </div>
    <div id="extras">
      
      <div id="more_menu" @click="toggleCard">
        <i class="fa fa-bars" aria-hidden="true" title="know more"></i>
      </div>
      <CountDown/>
      <div id="pictures" @click="showEngagementPics">
        <i class="fa fa-folder-open" aria-hidden="true" title="See engagement pics"></i>
      </div>
    </div>
    <SimpleCard :isOpen="isCardOpen" />
    <div class= "more_pics" v-if="isEngagementPicsShown">
      <p>here is a glance from our engagement</p>
      <img  id= "engagementPics" v-for="(slide, index) in engagementPics" :src="slide" :key="'slide-' + index"  />
    </div>
 
  </div>
  
  
</template>


<script>
import Carousal from './components/SlidingCarousal.vue'
import CardGallery from './components/CardGallery.vue'
import SimpleCard from './components/SimpleCard.vue'
import ConfettiExplosion from "vue-confetti-explosion"
import CountDown from "./components/CountDown.vue"



export default {
  name: 'App',
  components: {
    Carousal,
    CardGallery,
    SimpleCard,
    ConfettiExplosion,
    CountDown
  },
  mounted() {
    window.addEventListener('scroll', this.handleScroll);
    this.setMargin();
    window.addEventListener("resize", this.setMargin);
  },
  beforeUnmount() {
    window.removeEventListener('scroll', this.handleScroll);
    window.removeEventListener("resize", this.setMargin);
  },
  methods: {
    toggleCard() {
      this.isCardOpen = !this.isCardOpen;
      this.isEngagementPicsShown = false;
    },
    showEngagementPics() {
      this.isCardOpen = false;
      this.isEngagementPicsShown = !this.isEngagementPicsShown;
    },
    handleScroll() {
      const scrollPosition = window.scrollY;
      // Calculate opacity based on the scroll position (adjust the values as needed)
      this.imageOpacity = 1 - scrollPosition / 500;
    },
    setMargin() {
      const nameCard = document.querySelector('.name_card');
      if (window.innerWidth <= 767) {
        // Mobile view
        nameCard.style.marginTop = '60%';
      } else {
        // Desktop view
        nameCard.style.marginTop = '-30%';
      }
    },

  },
  
  data() {
    return {
      qrCode_knot: 'https://maps.app.goo.gl/PM8Yz1YPYF366S4d7',
      qrCode_bride: 'https://maps.app.goo.gl/fGBtyQceGQk9fyaw9',
      qrCode_groom: 'https://maps.app.goo.gl/ockwvxQjEQEXtQri7',
      slides: [
        require('@/assets/img/Onam_4.jpg'),
        require('@/assets/img/Onam_1.jpg'),
        require('@/assets/img/Onam_2.jpg'),
        require('@/assets/img/Onam_3.jpg') // Assuming you're using Vue CLI with @ alias
      ],
      engagementPics: [
        require('@/assets/img/1.jpg'), // Assuming you're using Vue CLI with @ alias
        require('@/assets/img/2.jpg'),
        require('@/assets/img/3.jpg'),
        require('@/assets/img/4.jpg'),
      ],
      isCardOpen: false,
      isEngagementPicsShown: false,
      showCarouselControls: false,
      imageOpacity: 1,
      cardSlides: [
        {
          eventTitle: 'Thalikettu',
          place: 'Puthuppara Temple,Vengola',
          location: 'https://maps.app.goo.gl/PM8Yz1YPYF366S4d7',
          eventDate: '28 Jan 2024',
          eventTime: '10:00 AM to 10:30 AM'
        },
        {
          eventTitle: 'Brides party',
          place: 'Salem Church Auditorium, Vengola',
          location: 'https://maps.app.goo.gl/jL6TwGpDybHLTZmn7',
          eventDate: '28 Jan 2024',
          eventTime: 'From 11:00 AM'
        },
        {
          eventTitle: 'Grooms Reception',
          place: 'Community Hall,Nellikuzhy',
          location: 'https://maps.app.goo.gl/G3Eeqj4bT2r3wXq17',
          eventDate: '28 Jan 2024',
          eventTime: '5:30 PM to 8.30 PM'
        }
      ]
    }
  },
  metaInfo() {
    return {
      title: 'Your Dynamic Title',
      meta: [
        { property: 'og:title', content: 'Gokul Weds Aswathy' },
        { property: 'og:description', content: 'Invitation to our wedding' },
        { property: 'og:image', content: process.env.BASE_URL + 'thumbnail.jpg' },
        { property: 'og:url', content: 'www.myweddinginviter.in/' },
        // Add other meta tags as needed
      ]
    };
  },
}
</script>

<style>
body{
  background:
        linear-gradient(rgb(245, 244, 244), transparent),
        linear-gradient(to bottom left, rgba(255, 89, 0, 0.353), transparent),
        linear-gradient(to bottom right,rgba(43, 234, 14, 0.577),transparent);
    background-blend-mode: screen;
}

#content {
  text-align: center;
  color: rgb(107, 43, 43);
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
   /* Adjust margin to prevent content overlap with the fixed image */
  padding: 20px;
}

.picture_cards {
  display: flex;
  /*font-family: 'Dancing Script', cursive;*/
  font-size: larger;
  align-items: center;
  justify-content: space-around;

}
.intro{
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  font-size: small;
}
.name_card {
  font-weight: bolder;
  font-size: large;
  font-family: 'Dancing Script', cursive; 
  text-shadow: 2px 2px 4px #000000;
  /* Additional styles as needed */
}
#main_pic {
  margin-bottom: 100%;
  display: flex;
  justify-content: center;
}
#main_pic img {
  position: fixed;
  opacity: 0.9;
  transition: opacity 0.5s ease;
  border-radius: 5px;
  border: 1px solid;
  max-width: 100%;
  top: 0;
  width:auto;
  height: auto;
  max-height:100%;
  z-index: -1;
  /* Send the image to the background */
}
.regards_msg{
  font-size: large;
  margin-bottom: 10px;
  font-family: 'Dancing Script', cursive;
}
.regards_msg button{
  background-color: transparent;
  border: 1px solid #291919; /* Change the border color as needed */
  border-radius: 50%;
  padding: 10px 20px;
  color:  rgb(107, 43, 43);
  cursor: pointer;
}
#extras{
  display: flex;
  position: relative;
  justify-content: space-around;
  font-family: cursive;
}
.more_pics{
  margin-top: 10%;

}
#engagementPics{
  max-width: 50%;
  max-height: 50%;
  margin: auto;
  justify-content: space-around;
  border: 2px;
  border-radius: 5px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);

}

</style>
