<template>
      <div class="countdown-number">We are counting !  {{ daysRemaining }} days more</div>
  </template>




<script>

export default {
  data() {
    return {
      targetDate: new Date('2024-01-28'), // Replace with your target date
    };
  },
  computed: {
    daysRemaining() {
      const currentDate = new Date();
      const timeDifference = this.targetDate - currentDate;
      const daysRemaining = Math.ceil(timeDifference / (1000 * 60 * 60 * 24));

      return daysRemaining > 0 ? daysRemaining : 0;
    },
  },
};
</script>





<style scoped>

.countdown-number {
  background-color:transparent;
  border-radius: 50%; /* Make it a circle */
  width: auto ;/* Set the width and height based on your design */
  height: auto;
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: 'Pacifico', cursive;
}

</style>
  